// ==========================================
// TYPES
// ==========================================

interface KBArticle {
  id: string
  title: string
  slug: string
  excerpt: string
  content: string
  category: string
  subcategory?: string
  tags: string[]
  status: 'draft' | 'published' | 'archived'
  visibility: 'public' | 'internal' | 'agents_only'
  views: number
  helpfulVotes: number
  notHelpfulVotes: number
  linkedTickets: number
  aiGenerated: boolean
  createdAt: string
  updatedAt: string
  publishedAt?: string
  author: string
}

interface KBCategory {
  id: string
  name: string
  slug: string
  description: string
  icon: string
  articleCount: number
  color: string
}

